@font-face {
  font-family: Noto Sans Black;
  src: url(./Components/Fonts/NotoSans-Black.ttf);
}
* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  width: 100%;
  user-select: none;
}
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(197, 196, 196); 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(245, 245, 245); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b7aeae; 
}