.Language {
  width: 100%;
  height: 100vh;
  padding-left: 100px;
  padding-right: 100px;
  font-family: Noto Sans Black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.Language h1 {
  text-align: center;
  font-size: 3rem;
  font-weight: 900;
  color: rgb(255, 191, 0);
}
.Language__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.LCB {
  border: rgba(255, 222, 173, 0);
  height: 6vh;
  width: 8vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(234, 234, 234);
  background-color: white;
  border-radius: 22px;
  font-size: 1.5rem;
  margin: 1%;
  font-family: "Noto Sans KR", sans-serif;
}
.LCB:hover {
  border: 1px solid rgb(255, 191, 0);
  color: rgb(33, 29, 29);
  background-color: rgb(255, 254, 247);
}
.Language a {
  display: flex;
  border-radius: 28px;
  margin-top: 5%;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: black;
  width: 14vmax;
  height: 5vmax;
  font-size: 12px;
}

.button2 {
  display: inline-block;
  transition: all 0.2s ease-in;
  position: relative;
  overflow: hidden;
  z-index: 1;
  color: #090909;
  padding: 0.7em 1.7em;
  font-size: 18px;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}

.button2:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
}

.button2:before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%) scaleY(1) scaleX(1.25);
  top: 100%;
  width: 140%;
  height: 180%;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.button2:after {
  content: "";
  position: absolute;
  left: 55%;
  transform: translateX(-50%) scaleY(1) scaleX(1.45);
  top: 180%;
  width: 160%;
  height: 190%;
  background-color: #009087;
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.button2:hover {
  color: #ffffff;
  border: 1px solid #009087;
}

.button2:hover:before {
  top: -35%;
  background-color: #009087;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

.button2:hover:after {
  top: -45%;
  background-color: #009087;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

@media (max-width: 600px) {
  .Language {
    margin-top: 0%;
    padding-left: 10px;
    padding-right: 10px;
  }
  .Language h1 {
    font-size: 2rem;
    margin-top: 2%;
  }
  .LCB {
    width: 28vw;
    height: 10vh;
    font-size: 1rem;
  }
  .Language a {
    width: 30vw;
    height: 10vh;
    font-size: 1rem;
  }
}
